.aae--video-story {
  display: flex;
  flex-direction: column;
}
.aae--video-story svg {
  width: 1em;
  height: 1em;
}
.aae--video-story img {
  object-fit: cover;
}
.aae--video-story .aae--posts {
  display: grid;
}
.aae--video-story .aae--post {
  position: relative;
  transition: all 0.3s;
}
.aae--video-story .aae--post::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition: all 0.3s;
}
.aae--video-story .aae--post:hover::after {
  opacity: 0;
  visibility: hidden;
}
.aae--video-story .aae--post:hover .content {
  opacity: 0;
  visibility: hidden;
}
.aae--video-story .aae--post video,
.aae--video-story .aae--post .thumb {
  width: 100%;
  height: 100%;
}
.aae--video-story .aae--post .thumb.expanded {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background-color: black;
  border-radius: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.aae--video-story .aae--post .thumb.expanded .close-btn {
  opacity: 1;
  visibility: visible;
}
.aae--video-story .aae--post .content {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  transition: all 0.3s;
  padding: 15px;
}
.aae--video-story .aae--post .wcf-post-title {
  margin-bottom: 15px;
}
.aae--video-story .aae--post .wcf-post-meta {
  margin-top: 15px;
  margin-bottom: 15px;
}
.aae--video-story .duration-wrap {
  display: flex;
  align-items: center;
  gap: 8px;
}
.aae--video-story .wcf-post-load-more {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border: 1px solid #1c1d20;
}
.aae--video-story .close-btn {
  color: white;
  font-size: 16px;
  border: 1px solid #333;
  width: 60px;
  height: 60px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 30px;
  right: 30px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}

body.vs-popup-opened {
  overflow: hidden;
}